@MenuCommand("edit", "edit-copy", "", ["ctrl", "c"])
export class EditCopyCommand implements IBaseMenuCommand {
    @OnMenuClick("edit-copy")
    action(ev: any): void {
        ElectronService.getInstance().showErrorMessageBox(
            "",
            "  ."
        );
    }
}
