import { ElectronService } from "../ElectronService";

export const GameMenuNameMap = <const>[
    "game-playtest",
    "game-fullscreen",
    "game-show-console",
    "game-folder-open",
];

export type GameMenuImpl = {
    name: string;
    children: {
        [key in typeof GameMenuNameMap[number]]: {
            name: string;
            children: Partial<Record<string, any>>;
            action?: (ev: any) => void;
        };
    };
};

export const GameMenu = <Partial<GameMenuImpl>>{
    name: "",
    children: {
        "game-playtest": {
            name: " ",
            children: {},
            action: (ev: any) => {
                alert("    .");
            },
        },
        "game-fullscreen": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
        "game-show-console": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
        "game-folder-open": {
            name: "  ",
            children: {},
            action: (ev: any) => {
                const service = new ElectronService();
                if (process.platform === "darwin") {
                    service.openFolder(__dirname);
                } else {
                    service.openFolder();
                }
            },
        },
    },
};
