export const ToolMenuNameMap = <const>[
    "tools-database",
    "tools-resource-manager",
    "tools-script-eidtor",
    "tools-sound-test",
    "tools-options",
];

export type ToolMenuImpl = {
    name: string;
    children: {
        [key in typeof ToolMenuNameMap[number]]: {
            name: string;
            children: Partial<Record<string, any>>;
            action: (ev: any) => void;
        };
    };
};

export const ToolMenu = <Partial<ToolMenuImpl>>{
    name: "",
    children: {
        "tools-database": {
            name: "",
            children: {},
            action: (ev: any) => {},
        },
        "tools-resource-manager": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
        "tools-script-eidtor": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
        "tools-sound-test": {
            name: " ",
            children: {},
        },
        "tools-options": {
            name: "",
            children: {},
            action: function (ev: any) {
                if (window.app) {
                    window.app.emit("openWindow", {
                        path: "/optionWindow",
                    });
                }
            },
        },
    },
};
