import { EmptySegment } from "./EmptySegment";
import { ToolbarBase } from "./interface/toolbar.dto";

const FileToolbar: ToolbarBase[] = [
    {
        name: " ",
        children: "file-new",
        action: (ev: any) => {
            if (window.app) {
                window.app.emit("openWindow", {
                    path: "/newWindow",
                });
            }
        },
    },
    {
        name: " ",
        children: "file-open",
        action: (ev: any) => {},
    },
    {
        name: " ",
        children: "file-save",
        action: (ev: any) => {},
    },
    {
        name: " ",
        children: "edit-undo",
        action: (ev: any) => {},
    },
    EmptySegment,
];

export { FileToolbar };
