export const HelpMenuNameMap = <const>["help-contents", "help-about"];

export type HelpMenuImpl = {
    name: string;
    children: {
        [key in typeof HelpMenuNameMap[number]]: {
            name: string;
            children: Partial<Record<string, any>>;
            action: (ev: any) => void;
        };
    };
};

export const HelpMenu = <Partial<HelpMenuImpl>>{
    name: "",
    children: {
        "help-contents": {
            name: "",
            children: {},
            action: (ev: any) => {
                alert("  .");
            },
        },
        "help-about": {
            name: " ",
            children: {},
            action: (ev: any) => {},
        },
    },
};
